.three-question-container {
  min-height: 550px;
  background-image: url(/img/home/backpack.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center right;
  padding: 2rem 0 0 0;
  position: relative; }
  .three-question-container .three-question-title {
    padding: 0.5rem 1rem;
    font-size: 1.5rem;
    background-color: rgba(255, 255, 255, 0.8);
    color: #344046; }
  .three-question-container .question-box {
    line-height: 5rem;
    font-size: 1.6875rem;
    color: #344046;
    min-height: 120px;
    padding: 0.5rem 1rem;
    margin-top: 6rem;
    background-image: url(/img/commons/folded-paper.png);
    background-repeat: no-repeat;
    background-size: 120% 120px;
    background-position: center left; }
  .three-question-container .question-option-panel {
    background-color: rgba(52, 64, 70, 0.3);
    color: #FFFFFF;
    font-size: 1.3125rem;
    padding: 2rem; }
    .three-question-container .question-option-panel > ol > li > a {
      color: #FFFFFF;
      letter-spacing: 1px;
      word-spacing: 0.25rem; }
  @media only screen and (min-width:40.063em) {
    .three-question-container {
      padding: 0; }
      .three-question-container .three-question-title {
        max-width: 320px;
        margin-left: 2rem; }
      .three-question-container .question-box {
        background-size: contain;
        max-width: 60%;
        padding-left: 2rem;
        position: relative;
        z-index: 1; }
      .three-question-container .question-option-panel {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 7%;
        min-width: 240px;
        max-width: 300px;
        width: 30%;
        z-index: 0; }
      .three-question-container .additional-info {
        color: #FFFFFF;
        margin-top: 2rem;
        padding-left: 2rem; }
        .three-question-container .additional-info > a.text-bold {
          color: #FFFFFF; } }
  @media only screen and (min-width:64.063em) {
    .three-question-container .question-option-panel {
      right: 10%;
      min-width: 270px; } }

.shellino-story {
  text-align: center; }

.story-container {
  max-width: 360px;
  margin: 0 auto; }

.story-title {
  text-transform: uppercase;
  color: #344046;
  font-size: 1.125rem; }

.story-image {
  max-height: 320px; }

@media only screen and (min-width:40.063em) {
  .shellino-story {
    text-align: left; }
  .story-container {
    max-width: none; } }

.sic-body {
  font-size: 1.3125rem; }

.sic-tile {
  background-color: #F0F0F0;
  border: 1px solid #CECEBF;
  padding: 0.5rem;
  margin: 10px 0; }

.sic-tile-body {
  margin-left: 27%; }

.sic-icons {
  width: 25%;
  fill: transparent;
  stroke: #006692;
  stroke-width: 5px;
  display: block;
  float: left; }

.sic-icons.icon-blog {
  stroke-width: 1px; }

.sic-icons.icon-service {
  stroke-width: 0.4px; }

.sic-icons.icon-history {
  stroke-width: 0.5px; }

.sic-tile-title {
  font-size: 1.125rem;
  text-decoration: underline; }
  .sic-tile-title > a {
    color: #4C5963; }

@media only screen and (min-width:40.063em) {
  .sic-tiles {
    display: flex;
    flex-wrap: wrap; }
  .sic-tile {
    width: 32%; }
  .sic-tile:nth-of-type(3n+2) {
    margin-left: 2%;
    margin-right: 2%; } }

.why-shellino-body {
  font-size: 1.5rem;
  font-weight: 300; }

